<template>
  <div>
    <v-app-bar app flat>

      <v-toolbar-title class="font-weight-bold">CCERMS</v-toolbar-title>

      <v-spacer></v-spacer>

      <v-btn
        text
        @click="toLogin"
      >
        Login
      </v-btn>
    </v-app-bar>

    <v-main style="padding: 0px;">
      <v-container>
        <v-row style="margin-bottom: 25px;">
          <v-col cols="12">
            <v-carousel hide-delimiters>
              <v-carousel-item
                v-for="(item,i) in items"
                :key="i"
                :src="item.src"
              ></v-carousel-item>
            </v-carousel>
          </v-col>
        </v-row>

        <v-row>
          <v-col cols="12">
            <h1 class="text-center text-decoration-underline title font-weight-bold">Research List</h1>
            <v-data-table
              :search="search"
              :headers="headers"
              :items="researcharchives"
              item-key="full_name"
              class="table-rounded"
              :loading="loadingPages"
            >
              <template v-slot:top>
                <v-text-field
                  v-model="search"
                  label="Search..."
                  class="mx-4"
                ></v-text-field>
              </template>
              <template #[`item.course`]="{item}">
                {{ `${item.course.course.toUpperCase()}` }}
              </template>
              <template #[`item.year_published`]="{item}">
                {{ `${item.year_from_published} - ${item.year_to_published}` }}
              </template>
              <template #[`item.adviser`]="{item}">
                <v-chip
                  v-for="(adv, index) in item.adviser"
                  :key="index"
                  small
                  class="font-weight-medium"
                >
                  {{ adv.fullname }}
                </v-chip>
              </template>
              <template #[`item.panel`]="{item}">
                <v-chip
                  v-for="(pan, index) in item.panel"
                  :key="index"
                  small
                  class="font-weight-medium"
                >
                  {{ pan.fullname }}
                </v-chip>
              </template>
              <template #[`item.member`]="{item}">
                <v-chip
                  v-for="(mem, index) in item.member"
                  :key="index"
                  small
                  class="font-weight-medium"
                >
                  {{ mem.fullname }}
                </v-chip>
              </template>
              <template #[`item.actions`]="{item}">
              <v-icon
                small
                @click="selectView(item)"
              >
                {{ icons.mdiMagnify  }}
              </v-icon>
            </template>
            </v-data-table> 
          </v-col>
        </v-row>
      </v-container>
    </v-main>
  </div>
</template>

<script>
import { mdiMagnify  } from '@mdi/js'
import {
  mapGetters,
  mapActions,
} from 'vuex'

  export default {
    data () {
      return {
        loadingPages: false,
        items: [
          {
            src: 'https://cdn.vuetifyjs.com/images/carousel/squirrel.jpg',
          },
          {
            src: 'https://cdn.vuetifyjs.com/images/carousel/sky.jpg',
          },
          {
            src: 'https://cdn.vuetifyjs.com/images/carousel/bird.jpg',
          },
          {
            src: 'https://cdn.vuetifyjs.com/images/carousel/planet.jpg',
          },
        ],

        search: '',
        headers: [
          { text: 'RESEARCH TITLE', value: 'title' },
          { text: 'COURSE', value: 'course' },
          { text: 'TAGS', value: 'tags' },
          { text: 'YEAR PUBLISHED', value: 'year_published' },
          { text: 'ADVISERS', value: 'adviser' },
          { text: 'PANELS', value: 'panel' },
          { text: 'MEMBERS', value: 'member' },
          { text: 'ACTIONS', value: 'actions', sortable: false },
        ],

        icons: {
          mdiMagnify,
        },
      }
    },
    mounted() {
      this.getResearcharchives().then(() => {
        this.loadingPages = false
      })
    },
    computed: {
      ...mapGetters(['researcharchives']),
    },
    methods: {
      toLogin() {
        this.$router.push('/login')
      },
      selectView(item) {

      },
      ...mapActions(['getResearcharchives']),
    }
  }

</script>
